import { h, ref, reactive, onMounted } from "vue";
import { ElNotification } from "element-plus";
import 'element-plus/es/components/notification/style/index'

import axios from "../../../shared/axios.js";
export default {
  props: {
    isUrgency: {
      type: Boolean,
      required: false,
      default: false
    },
    isAdministrator: {
      type: Boolean,
      required: false,
      default: false
    },
    table: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    }
  },

  setup(props) {
    const tableData = ref([]);

    const handleError = () => {
      ElNotification({
        title: "Ошибка",
        message: h("i", {
          style: "color: teal"
        }, `Ошибка в таблице ${props.label}`),
        type: "error"
      });
    };

    const getTableData = () => {
      axios.get(`/dashboard/control/${props.table}`).then(response => {
        tableData.value = response.data;
      }).catch(() => {
        handleError();
      });
    };

    const deleteRow = index => {
      axios.delete(`/dashboard/control/${props.table}`, {
        data: {
          id: tableData.value[index].value
        }
      }).then(() => {
        getTableData();
        ElNotification({
          title: "Успешно удалено",
          message: h("i", {
            style: "color: teal"
          }, `Удалено в таблице ${props.label}`),
          type: "success"
        });
      }).catch(() => {
        handleError();
      });
    };

    const toggleEdit = index => {
      tableData.value[index].edit = !tableData.value[index].edit;
      tableData.value[index].editValue = tableData.value[index].label;
      tableData.value[index].editInterval = tableData.value[index].interval;
    };

    const editRow = index => {
      const data = {
        id: tableData.value[index].value,
        field: tableData.value[index].editValue
      };

      if (props.isUrgency) {
        data.interval = tableData.value[index].editInterval;
      }

      axios.patch(`/dashboard/control/${props.table}`, data).then(() => {
        getTableData();
        ElNotification({
          title: "Успешно изменено",
          message: h("i", {
            style: "color: teal"
          }, `Заменено в таблице ${props.label}`),
          type: "success"
        });
      }).catch(() => {
        handleError();
      });
    };

    const formRef = ref(null);
    const addForm = reactive({
      value: null,
      interval: null
    });
    const rules = {
      value: [{
        required: true,
        message: "Пожалуйста, укажите значение",
        trigger: "blur"
      }],
      interval: [{
        required: true,
        message: "Пожалуйста, укажите интервал",
        trigger: "blur"
      }]
    };

    const addRow = () => {
      formRef.value.validate(valid => {
        if (valid) {
          const data = props.isUrgency ? {
            field: addForm.value,
            interval: addForm.interval
          } : {
            field: addForm.value
          };
          axios.post(`/dashboard/control/${props.table}`, data).then(() => {
            getTableData();
            ElNotification({
              title: "Успешно добавлено",
              message: h("i", {
                style: "color: teal"
              }, `Добавлено в таблицу ${props.label}`),
              type: "success"
            });
          }).catch(() => {
            handleError();
          });
          return true;
        }

        return false;
      });
    };

    onMounted(() => {
      getTableData();
    });
    return {
      tableData,
      deleteRow,
      toggleEdit,
      editRow,
      formRef,
      addForm,
      rules,
      addRow
    };
  }

};