import "core-js/modules/web.dom-collections.iterator.js";
import "core-js/modules/es.regexp.exec.js";
import "core-js/modules/es.string.replace.js";
import { ref, reactive, onMounted, watch } from "vue";
import { useRouter, useRoute } from "vue-router";
import { UserFilled, Menu } from "@element-plus/icons-vue";
import BaseMenu from "../components/BaseMenu.vue";
import ControlsTable from "../components/ControlsTable.vue";
import axios from "../../../shared/axios.js";
export default {
  components: {
    BaseMenu,
    ControlsTable
  },

  setup() {
    const router = useRouter();
    const route = useRoute();
    const drawer = ref(false);
    const refs = ref({});

    const toggleDrawer = () => {
      drawer.value = !drawer.value;
    };

    const logout = () => {
      axios.post("/dashboard/auth/logout").then(() => {
        router.push("/dashboard/login");
      });
    };

    const scrollToAnchorPoint = refName => {
      const el = refs.value[refName];
      el.scrollIntoView({
        behavior: "smooth"
      });
    };

    const state = reactive({
      sectionObserver: null
    });

    const sectionObserverHandler = entries => {
      // eslint-disable-next-line no-restricted-syntax
      for (const entry of entries) {
        if (entry.isIntersecting) {
          const sectionId = entry.target.id; // Push sectionId to router here

          router.replace({
            name: route.name,
            hash: `#${sectionId}`,
            query: {
              isObserver: true
            }
          });
        }
      }
    };

    const observeSections = () => {
      const options = {
        rootMargin: "0px 0px",
        threshold: 0.5
      };
      state.sectionObserver = new IntersectionObserver(sectionObserverHandler, options); // Observe each section

      const sections = document.querySelectorAll(".section");
      sections.forEach(section => {
        state.sectionObserver.observe(section);
      });
    };

    watch(() => router.currentRoute.value.hash, () => {
      if (router.currentRoute.value.hash && !router.currentRoute.value.query.isObserver) {
        scrollToAnchorPoint(router.currentRoute.value.hash.slice(1));
      }
    });
    onMounted(() => {
      if (router.currentRoute.value.hash) {
        scrollToAnchorPoint(router.currentRoute.value.hash.slice(1));
      }

      observeSections();
    });
    return {
      drawer,
      refs,
      toggleDrawer,
      logout,
      UserFilled,
      Menu
    };
  }

};